import React, {Component} from 'react';
import {connect} from 'react-redux';

import * as actionCreators from '../../store/actions/index';

import Spinner from '../../components/UI/Spinner/Spinner';

import './DragAndDrop.css';

class DragAndDrop extends Component {
	state = {
		data: null
	};

	componentDidMount() {
		this.props.fetchData(
			`https://spreadsheets.google.com/feeds/list/1n5633PymXXc-qW_UWMMFxaJYpR-r5PoXrGMaOn7VfF0/${this.props.location.hash.slice(1)}/public/values?alt=json`
		);
					// `https://spreadsheets.google.com/feeds/list/1n5633PymXXc-qW_UWMMFxaJYpR-r5PoXrGMaOn7VfF0/10/public/values?alt=json`

	}

	componentDidUpdate(prevProps, prevState) {
		if(prevProps && prevProps !== this.props) {
			this.setState({data: this.props.data});
		}
	}

	shiftElement = id => {
	  let elem = document.querySelector(`[data-id="${id}"]`);
	  return elem.parentNode.removeChild(elem);
	};

	checkIfCorrect = e => {
	  if(e.target.firstElementChild.dataset.id === e.target.parentNode.dataset.value) {
	    e.target.classList.add('-correct');
	    e.target.parentNode.classList.add('-correct');
	    e.target.firstElementChild.draggable = false;
	  }else {
	    e.target.classList.add('-wrong');
	  }
	};

	onDragStartHandler = e => {
	  e.dataTransfer.setData('text/plain', e.target.dataset.id);
	  e.dataTransfer.effectAllowed = 'move';
	};

	onDragoverHandler = e => {
	  if(e.dataTransfer.types[0] === 'text/plain') {
	   e.preventDefault();
	    if(e.target.classList.contains('droppable')) {
	    //   e.toElement.classList.add('-active');
	    }
	  }
	};

	onDragEnterHandler = e => {
	  if(e.dataTransfer.types[0] === 'text/plain') {
	   e.preventDefault(); 
	      if(e.target.classList.contains('droppable')) {
	    //   e.toElement.classList.add('-active');
	    }
	  }
	};

	onDraagLeaveHandler = e => {
	  if(e.target.classList.contains('droppable') && !e.target.children.length) {
	   e.target.classList.remove('-active', '-correct', '-wrong'); 
	  }
	};

	onDropHandler = e => {
	  let condition = e.target.classList.contains('draggable-container') || e.target.classList.contains('box');
	  if(!condition) return;
	  let id = e.dataTransfer.getData('text/plain');
	  e.target.appendChild(this.shiftElement(id));
	  this.checkIfCorrect(e);
	};

	render() {
		let data = null;
		if(this.props.loading) {
			data = <Spinner />;
		}
		if(this.state.data) {
			data = (
				<div className="DragAndDrop">
					{/* <h1>{this.state.title}</h1> */}
					<div className="drag-and-drop"
						onDragStart={this.onDragStartHandler}
						onDragEnter={this.onDragEnterHandler}
						onDragLeave={this.onDraagLeaveHandler}
						onDragOver={this.onDragoverHandler}
						onDrop={this.onDropHandler}
					>
						<div className="draggable-container">
							{this.state.data.map(el => (
								<span className="element" data-id={el.word} draggable="true" key={el.word}>{el.word}</span>
							))}
						</div>
						<div className="droppable-container">
							{this.state.data.filter(el => el.opposite ? el.opposite : null).map((sentense, index) => (
								<span className='sentense' data-value={this.state.data[index].correctword} key={this.state.data[index].correctword}>
									{sentense.opposite.split('|+|')[0]}
									<span className="box droppable" />
									{sentense.opposite.split('|+|')[1]}
									{/* {`${index + 1}. ${sentense.opposite.replace('|+|', <span className="box droppable" />)}`} */}
								</span>
							))}
						</div>
					</div>
				</div>
			);
		}
		return data;
	}
}

const mapStateToProps = state => {
	return {
		data: state.wordsConstructor.fetchedData,
		loading: state.wordsConstructor.loading,
		error: state.wordsConstructor.error
	};
};

const mapDispatchToProps = dispatch => {
	return {
	  fetchData: url => dispatch(actionCreators.asyncFetchStart(url))
	};
  };

export default connect(mapStateToProps, mapDispatchToProps)(DragAndDrop);



